import React from "react";
import {auth, getUser, signOut} from '../../../../firebase/firebase'
import {NextPage} from "../UserPage";

class TempManager extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            isLoad:false,
            user: props.location,
            error:false,
            loading: true,
            rule:"Manager",
        };
    }


    loadPage(event){
        this.setState({loading:event})
        //    this.render()
    }

    async componentDidMount() {
        auth.onAuthStateChanged(async user=>{
            if(user)
            {
                var type = await getUser(user)
                console.log(type)
                if(type)
                {
                    this.setState({
                        isLoad: true,
                        user: user,
                        type: type
                    })
                    if(type!=='Tester')
                        this.loadUser(type)
                }
                else{
                    alert('     ')
                    window.location.href = '/Login';
                    return
                }
                // console.log(tester.exists)
                // console.log(user)
                console.log("change user")
                // this.setState({
                //     isLoad:true,
                //     user:user,
                // })

            }
            else {
                this.setState({
                    isLoad: true,
                })
                window.location.href = '/Login';
                return;

            }
            this.render()
        })

    }


    async  logout() {
        // 
        await auth.signOut();
        window.location.href = '/';
        //  
    }



    render() {

        return (
            <div id="instructor" className="sec-design" dir='rtl'>
                 <div>  {this.state.user.displayName}</div>
                <button id="report-button" className="btn btn-info"onClick={()=>{
                    this.ChangePage("UserApproval")
                    return
                }} > <span
                    className="fa fa-arrow-right"></span></button>
                <button id="report-button" className="btn btn-info"onClick={()=> {
                    this.ChangePage("Updates")
                    return
                }} > <span
                    className="fa fa-arrow-right"></span></button>

                <button id="report-button" className="btn btn-info" onClick={()=>{
                    this.ChangePage("Reports")
                    return
                }} > " <span
                    className="fa fa-arrow-right"></span></button>
                <button id="feedback-button" className="btn btn-info" onClick={()=>{
                    this.ChangePage("Feedbacks/Student")
                    return
                }} >  <span
                    className="fa fa-arrow-right"></span></button>
                <button id="feedback-button" className="btn btn-info" onClick={()=>{
                    this.ChangePage("Feedbacks/Guide")
                    return
                }}> 
                    <span
                        className="fa fa-arrow-right"></span></button>
                <button id="report-button" className="btn btn-info" onClick={()=>{NextPage(this.props,"Profile",this.state.user)}} >   <span
                        className="fa fa-arrow-right"></span></button>
                <button id="logout" className="btn btn-info" onClick={()=>{signOut()}} ></button>
                <button onClick={() => this.loadTempPage("User")}>   </button>
            </div>

        );
    }

    loadTempPage(path)
    {
        this.props.history.push({
            pathname: `/${path}`,
            data: this.state.user
        })
    }

    ChangePage(path) {

        this.props.history.push({
            pathname: `${this.props.location.pathname}/${path}`,
            data: this.state.user
        })

    }

    loadUser(page)
    {
        this.props.history.push({
            // pathname: `/${page}/${this.state.user.id}`,
            pathname: `/Temp${page}`,
            data: this.state.user // your data array of objects
        })
    }
}


export  default  TempManager;