import React from 'react'
import Select from 'react-select'
import {db, admin, CreateUser, DeleteUser, auth, getUser} from '../../../../firebase/firebase'
import Grid from "@material-ui/core/Grid";
import '../Guide/Guide.css';
import TempManager from "./TempManager";
import {BackPage} from "../UserPage";
import ClipLoader from "react-spinners/ClipLoader";


const options = [
   ]


class UserApproval extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            spinner: [true,'   '],

        };

    }

    loadSpinner(event,massage){
        var spinner = []
        spinner.push(event)
        spinner.push(massage)
        this.setState({spinner:spinner})
    }


    async componentDidMount() {
        auth.onAuthStateChanged(async user=>{
            if(user)
            {
                var type = await getUser(user)
                console.log(type)
                if(type)
                {
                    this.setState({
                        isLoad: true,
                        user: user,
                        type: type
                    })
                    // if(type!=='Tester')
                    //     this.loadUser(type)
                }
                else{
                    alert('     ')
                    window.location.href = '/Login';
                    return
                }
                // console.log(tester.exists)
                // console.log(user)
                console.log("change user")
                // this.setState({
                //     isLoad:true,
                //     user:user,
                // })

            }
            else {
                this.setState({
                    isLoad: true,
                })
                window.location.href = '/Login';
                return;

            }
            this.loadSpinner(false,"")
            this.render()
        })
        this.loadSpinner(true,"  ")
        const collection = await db.collection('waitforapproval').get()
        const usersList = [];
        collection.forEach(doc => {
            const data = doc.data();
            if (data)
                usersList.push(data)
        });
        this.setState({users: usersList});

        var nameTeams =  await db.collection("Teams").get();
            nameTeams.forEach(doc=>{
                options.push({ value: doc.ref, label: doc.data().name })
            })

        this.loadSpinner(false,"")

    }




    radio(e,index,user)
{

    var student = document.getElementById("students"+index)
    var guide = document.getElementById("guides"+index)
    var manager = document.getElementById("managers"+index)
    var tester = document.getElementById("testers"+index)


    user.type = e.target.value;

    if(e.target === student) {
        student.checked=true;
        guide.checked=false;
        manager.checked=false;
        tester.checked=false;

    }
    else if(e.target === guide) {
        student.checked=false;
        guide.checked=true;
        manager.checked=false;
        tester.checked=false;

    }
    else if(e.target === manager) {
        student.checked=false;
        guide.checked=false;
        manager.checked=true;
        tester.checked=false;

    }
    else {
        student.checked=false;
        guide.checked=false;
        manager.checked=false;
        tester.checked=true;
    }
}
render() {
        
    if(this.state.page ==='menu')
        return(
            <div>
                {!this.state.spinner[0] ? "" :
                    <div id='fr'>
                        {this.state.spinner[1]}
                        <div className="sweet-loading">
                            <ClipLoader style={{
                                backgroundColor: "rgba(255,255,255,0.85)",
                                borderRadius: "25px"
                            }}
                                //   css={override}
                                        size={120}
                                        color={"#123abc"}

                            />
                        </div>
                    </div>
                }
            <TempManager>

            </TempManager>
            </div>
        )
        if(this.state.users) {
            if(this.state.users.length === 0)
            {
                return (
                    <div id="guideAttendReport" className="sec-design" dir="rtl">
                        {!this.state.spinner ? "" :
                            <div id='fr'>
                                 /  
                                <div className="sweet-loading">
                                    <ClipLoader style={{
                                        backgroundColor: "rgba(255,255,255,0.85)",
                                        borderRadius: "25px"
                                    }}
                                        //   css={override}
                                                size={120}
                                                color={"#123abc"}
                                    />
                                </div>
                            </div>
                        }
                        <div id="name-group" className="form-group">
                            <Grid container spacing={2}>
                                <Grid item xs={12}>
                                      
                                </Grid>
                            </Grid>
                            <Grid item xs={12}>
                                <button id="feedback-button" className="btn btn-info"  onClick={()=>{BackPage(this.props,this.state.user)}}> </button>
                            </Grid>
                        </div>
                    </div>
                )
            }
            return (
                <div id="guideAttendReport" className="sec-design" dir="rtl">
                    {!this.state.spinner ? "" :
                        <div id='fr'>
                             /  
                            <div className="sweet-loading">
                                <ClipLoader style={{
                                    backgroundColor: "rgba(255,255,255,0.85)",
                                    borderRadius: "25px"
                                }}
                                    //   css={override}
                                            size={120}
                                            color={"#123abc"}
                                />
                            </div>
                        </div>
                    }
                    <div id="name-group" className="form-group">
                        <Grid container spacing={2}>
                            <Grid item xs={12}>
                                <Grid container
                                      direction="row"
                                      justify="space-between"
                                      alignItems="center"
                                      spacing={2}>

                                    {this.state.users.map((user,index) => (
                                        <Grid item xs={12} key={index}>
                                            {this.Card(user, index)}
                                        </Grid>
                                        )
                                    )}


                                </Grid>
                                {/*<Grid item xs={12}>*/}
                                {/*    <div className="text-below-image">*/}
                                {/*        <button>  </button>*/}
                                {/*    </div>*/}
                                {/*</Grid>*/}
                                <Grid item xs={12}>
                                    <button id="feedback-button" className="btn btn-info"  onClick={()=>{BackPage(this.props,this.state.user)}}> </button>
                                </Grid>
                            </Grid>
                        </Grid>
                    </div>


                    {/*<button id="feedback-button" className="btn btn-info"  onClick={()=>{this.chooseLayout}}> <span className="fa fa-arrow-right"></span></button>*/}


                </div>
            );
        }
        else
        {
           return (
               <div id="guideAttendReport" className="sec-design" dir="rtl">
                   {!this.state.spinner ? "" :
                       <div id='fr'>
                            /  
                           <div className="sweet-loading">
                               <ClipLoader style={{
                                   backgroundColor: "rgba(255,255,255,0.85)",
                                   borderRadius: "25px"
                               }}
                                   //   css={override}
                                           size={120}
                                           color={"#123abc"}
                               />
                           </div>
                       </div>
                   }
                   <div id="name-group" className="form-group">
                       <Grid container spacing={2}>
                           <Grid item xs={12}>
                                
                           </Grid>
                       </Grid>
                   </div>
               </div>
           )
        }
}




    Card(user,index) {
            return (
                <div className="Card"  dir="rtl">
                    <Grid container spacing={1}>
                        <Grid item xs={12}>
                        <b> : </b>  {user.fname + " " + user.lname}<br/>
                            <b> : </b> {user.email}<br/>
                            <b> : </b>{user.phone}<br/>
                            {
                                (user.type === "students") ?
                                    (<b> :  </b>):(user.type === "guides")?
                                    (<b> :  </b>):(user.type === "managers")?
                                        (<b> :  </b>):(<b> :  </b>)

                            }
                            <br/>
                            <b> : </b>{user.teamName}<br/>
                            <Select  placeholder={"   "} options={options} onChange={(e)=>{
                                user.team = e.value;
                                user.teamName = e.label;
                            }} />
                        </Grid>
                        <Grid item xs={12}>
                        <b>  : </b><br/>
                        </Grid>
                        <Grid item xs={3}>

                            <div>
                                <label>
                                    <input id ={"students"+index} type="radio" value="students" onClick={e => {
                                        this.radio(e,index,user)
                                    }}/>
                                    
                                </label>
                            </div>
                        </Grid>
                        <Grid item xs={3}>
                            <div>

                                <label>
                                    <input id ={"guides"+index} type="radio" value="guides"  onClick={e => {
                                        this.radio(e,index,user)
                                    }}/>
                                    
                                </label>
                            </div>
                        </Grid>
                        <Grid item xs={3}>
                            <div>

                                <label>
                                    <input id ={"managers"+index} type="radio" value="managers"  onClick={e => {
                                        this.radio(e,index,user)
                                    }}/>
                                    
                                </label>
                            </div>
                        </Grid>
                        <Grid item xs={3}>
                            <div>

                                <label>
                                    <input id ={"testers"+index} type="radio" value="testers"  onClick={e => {
                                        this.radio(e,index,user)
                                    }}/>
                                    
                                </label>
                            </div>
                        </Grid>
                        <Grid item xs={12}>
                            <div className="text-below-image">
                                <label className="container"> <input type='checkbox'  onChange={e=>{
                                    user.approve = e.target.checked
                                } }/></label>
                            </div>

                        </Grid>

                        <Grid item xs={6}>
                            <button
                                style={{backgroundColor: "rgb(50,248,6)"}}
                                onClick={()=>{
                                CreateUser(user).then(()=>{
                                var newUsers = []
                                this.state.users.forEach((user,i)=>{
                                 if(index!==i)
                                     newUsers.push(user)
                                })
                                this.setState({users:newUsers})
                                })
                            }}>  </button>
                        </Grid>
                        <Grid item xs={6}>
                            <button
                                style={{backgroundColor: "rgb(248,6,6)"}}
                                onClick={()=>{
                                DeleteUser(user.uid).then(()=>{
                                    var newUsers = []
                                    this.state.users.forEach((user,i)=>{
                                        if(index!==i)
                                            newUsers.push(user)
                                    })
                                    this.setState({users:newUsers})
                                })
                            }}> </button>
                        </Grid>
                    </Grid>




                </div>
            );
    }

    loadUser(page)
    {
        this.props.history.push({
            // pathname: `/${page}/${this.state.user.id}`,
            pathname: `/Temp${page}`,
            data: this.state.user // your data array of objects
        })
    }

}

export  default  UserApproval;